
.customize{
    animation: fadeIn 1s;
    transition: 1s;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    max-width: 500px;
    border-radius: 50%;
    min-width: 200px;
    background-color: #f5f5f5;
    padding-top: 20px;
    position: fixed;
    bottom: 0;
    right: 0;
    z-index: 1;


    @keyframes fadeIn {
        from {
            opacity: 0;
        }
        to {
            opacity: 1;
        }
    }

    .customize-header{
        width: 100%;

        h1{
            text-align: center;
            width: 100%;
        }
    }
    

    .customize-body{
        width: 100%;

        form{
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            gap: 30px;
            background-color: #f5f5f5;
            padding: 20px;

            input[type="color"]{
                margin-top: 7px;
                width: 100%;
                height: 30px;
                border-radius: 5px;
                justify-self: center;
                border: 1px solid rgb(143, 143, 143);
                padding: 5px;
                font-size: 1.2rem;
                outline-style: none;
                outline-color: transparent;
                background-color: #f5f5f5;
                color: #000;
                transition: all 0.3s ease-in-out;
            }

            .wrap-color, .wrap-fonts{
                display: flex;
                align-items: center;
                justify-content: center;
                width: 100%;
                gap: 20px;
                margin-bottom: 1rem;
            }
        }
        
    }
}