#enableSettings {
  background-color: transparent;
  border: none;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  cursor: pointer;
  position: fixed;
  bottom: 30px;
  right: 30px;
  z-index: 2;
  background-image: url("../assets/icons8-settings-64.png");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
}
#enableSettings abbr {
  background-color: transparent;
  width: 100%;
  height: 100%;
}

#enableSettings:hover {
  -webkit-animation: enablesettings 1s;
          animation: enablesettings 1s;
}

@-webkit-keyframes enablesettings {
  0% {
    transform: rotate(0deg) scale(1);
  }
  50% {
    transform: rotate(180deg) scale(1.5);
  }
  100% {
    transform: rotate(360deg) scale(1);
  }
}

@keyframes enablesettings {
  0% {
    transform: rotate(0deg) scale(1);
  }
  50% {
    transform: rotate(180deg) scale(1.5);
  }
  100% {
    transform: rotate(360deg) scale(1);
  }
}
